import {Component, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {AdminsServices} from '../../../services/admins/admins.services';
import {ActivatedRoute, Router} from '@angular/router';
import {AdminColumn} from './admin.column';
import {GridDataResult} from '@progress/kendo-angular-grid';
import {NbDialogService, NbToastrService} from '@nebular/theme';
import {FilterToQueryService} from '../../../services/filter-to-query/filter-to-query.service';
import {State} from '@progress/kendo-data-query';
import {saveAs} from 'file-saver';
import {SharedApiServiceService} from '../../../services/shared/shared-api-service.service';

@Component({
    selector: 'app-admin',
    templateUrl: 'admins.component.html',
    styleUrls: ['admins.component.scss'],
})
export class AdminsComponent implements OnInit {
    @ViewChild('dialog') dialog: any;

    columnGrid: any = AdminColumn;
    dataItem: GridDataResult = {
        data: [],
        total: 0,
    };
    lastMeta: string;
    loading: boolean;
    autoCompleteData: any = {
        name: 'category_brand',
        list: []
    };
    currentDeleteAdmin: any;
    isLoading: boolean = false;

    constructor(
        private adminService: AdminsServices,
        private toaster: NbToastrService,
        private activeRouter: ActivatedRoute,
        private convertFilter: FilterToQueryService,
        private router: Router,
        private dialogService: NbDialogService,
        private sharedService: SharedApiServiceService
    ) {
    }

    ngOnInit() {
        this.getAdmins();
    }

    getAdmins(meta?: string) {
        this.isLoading = true;
        this.adminService.getAdmins(meta).subscribe(
            res => {
                this.dataItem.data = res['data'];
                this.dataItem.total = res['total'];
            },
            error => {
                this.isLoading = false;
            },
            () => {
                this.isLoading = false;
            }
        );
    }

    deleteAdmin(dialogRef: any) {
        this.adminService.deleteAdmin(this.currentDeleteAdmin.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', {status: 'success'});
            },
            error => {
                this.toaster.show(error.error.message, 'Error', {status: 'danger'});
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getAdmins(this.lastMeta);
                this.currentDeleteAdmin = null;
            },
        );
    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['./admins/edit-admin/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteAdmin = event.data;
                this.open(this.dialog);
                break;
        }
    }

    open(dialog: TemplateRef<any>) {
        this.dialogService.open(dialog);
    }

    filterChange(event: State) {
        this.getAdmins(this.convertFilter.convertToQuery(event));
    }

    export() {
        this.sharedService.exportUrl = this.lastMeta ? 'admins/export/excel?' + this.lastMeta : 'admins/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'admins.xlsx');
        }),
            error =>  this.toaster.show('Error downloading the file.', 'Error', {status: 'danger'});
    }
}
