import { Component, OnInit } from '@angular/core';
import { AdminsServices } from '../../../services/admins/admins.services';
import { ActivatedRoute, Router } from '@angular/router';
import { RolesServices } from '../../../services/roles/roles.services';
import { NbToastrService } from '@nebular/theme';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { DropzoneConfigInterface } from 'ngx-dropzone-wrapper';

@Component({
  selector: 'app-edit-admin',
  templateUrl: 'edit-admin.component.html',
  styleUrls: ['edit-admin.component.scss'],
})
export class EditAdminComponent implements OnInit {

  adminFormGroup: FormGroup;

  public dropzoneConfig: DropzoneConfigInterface = {
    paramName: 'file', // The name that will be used to transfer the file
    maxFilesize: 2, // MB
    url: '/',
    autoQueue: false,
    autoProcessQueue: false,
    uploadMultiple: false,
  };

  title: string = ""
  adminId: any;
  roles: any = [];
  isLoading = false;
  selectedImage: File;
  adminImage: string = '';

  constructor(private adminService: AdminsServices,
    private activeRouter: ActivatedRoute,
    private roleService: RolesServices,
    private toaster: NbToastrService,
    private route: Router) {

    this.adminFormGroup = new FormGroup({
      id: new FormControl(),
      name: new FormControl('', [Validators.required, Validators.minLength(2), Validators.maxLength(16), Validators.pattern('^[a-zA-Z-\'.+-, \s]*$')]),
      email: new FormControl('', [Validators.required, Validators.pattern(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]),
      mobile: new FormControl('', [Validators.required, Validators.minLength(11)]),
      password: new FormControl(),
      rePassword: new FormControl(),
      activation: new FormControl(false),
      role: new FormControl(''),
      roleId: new FormControl('', Validators.required),
      type_id: new FormControl(),
      image: new FormControl(),
    })
  }

  ngOnInit() {
    this.getRoles();
    this.activeRouter.params.subscribe(v => this.adminId = v.id);
    if (this.adminId) {
      this.title = "Edit";
      this.getAdmin();
    } else {
      this.title = "Add";
      this.adminFormGroup.get('password').setValidators(Validators.required);
      this.adminFormGroup.get('rePassword').setValidators(Validators.required);
    }
  }

  getRoles() {
    this.roleService.getRoles().subscribe(
      res => {
        this.roles = res['roles'];
      },
      error => { },
      () => {
      },
    );
  }

  getAdmin() {
    this.isLoading = true;
    this.adminService.getAdmin(this.adminId).subscribe(
      res => {
        let data = res['data'];
        data.password = '';
        data.rePassword = '';
        this.adminFormGroup.setValue(data);
        this.adminImage = res['data'].image
      },
      error => {
        this.toaster.show(error.error.message, 'Error', { status: 'danger' });
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  prepareParams() {
    const newAdminData: any = this.adminFormGroup.value;
    const uploadData = new FormData();
    if (this.selectedImage) {
      uploadData.append('image', this.selectedImage, this.selectedImage.name);
    }
    uploadData.append('name', newAdminData.name);
    uploadData.append('email', newAdminData.email);
    uploadData.append('mobile', newAdminData.mobile);
    uploadData.append('activation', newAdminData.activation);
    uploadData.append('roleId', newAdminData.roleId);

    if (this.adminId) {
      uploadData.append('id', this.adminId);
    } else {
      uploadData.append('password', newAdminData.password);
    }

    return uploadData;
  }

  editAdmin() {
    if (this.adminFormGroup.valid) {
      this.isLoading = true;

      let response = {};
      let uploadData = this.prepareParams();

      this.adminService[this.adminId ? 'editAdmin' : 'CreateAdmin'](uploadData).subscribe(
        res => {
          this.toaster.show(res['msg'], 'Success', { status: 'success' });
          response = res['data'];
          this.route.navigateByUrl('/admins');
        }, error => {
          this.isLoading = false;
          this.toaster.show(error.error.message, 'Error', { status: 'danger' });
        },
        () => {
          this.isLoading = false;
          this.updateLoggedInData(response);
        }
      );
    } else {
      this.toaster.show('Form Not Valid', 'Error', { status: 'danger' });
    }
  }

  updateLoggedInData(res) {
    let loggedInAdmin = JSON.parse(localStorage.getItem('admin'));

    if (loggedInAdmin['user_id'] === Number(this.adminId)) {

      if (loggedInAdmin['name'] !== this.adminFormGroup.value['name']) {
        loggedInAdmin.name = this.adminFormGroup.value['name'];
        localStorage.setItem('admin', JSON.stringify(loggedInAdmin))
      }

      if (this.selectedImage) {
        let imageURL = 'https://weshop-img.s3.eu-west-3.amazonaws.com' + res.image;
        loggedInAdmin.image = imageURL;
        localStorage.setItem('admin', JSON.stringify(loggedInAdmin))
      }
    }
  }

  onCategoryImageChange(event: any) {
    this.selectedImage = event;
  }

  clearImage() {
    this.selectedImage = null;
    this.adminImage = '';
  }

  validateForm() {
    if (this.adminFormGroup.invalid) return true;
    if (this.adminImage?.length == 0 && !this.selectedImage) return true;
    if (this.adminFormGroup.get('password').value !== this.adminFormGroup.get('rePassword').value) return true;

    return false;
  }

  backToAdmin() {
    this.route.navigate(['admins'])
  }
}
